import { EffectCallback, useEffect } from 'react'
import { push } from 'connected-react-router'
export const useMounted = (fn: EffectCallback) => {
  /* eslint-disable react-hooks/exhaustive-deps */
  useEffect(fn, [])
  return null
}

type S = Record<string, string>
export const usePush = () => {
  return ({ pathname, search, state }: { pathname: string; search?: string | S; state?: S }) => {
    const { hash } = window.location
    let searchName = hash.split('?')[1] || ''
    if (Object.prototype.toString.call(search) === '[object Object]') {
      for (const k in search as unknown as S) {
        searchName += `&${k}=${(search as S)[k]}`
      }
    } else if (Object.prototype.toString.call(search) === '[object String]') {
      searchName += search
    }
    push({ pathname, search: searchName, state })
  }
}
