function autoPlayAudio(voice:HTMLAudioElement) {
            let voicePromise = voice.play();
            //判斷 WeixinJSBridge 是否存在
            if (typeof WeixinJSBridge == "object" && typeof WeixinJSBridge.invoke == "function") {
                voicePromise =voice.play();
            } else {
                //監聽客户端抛出事件"WeixinJSBridgeReady"
                if (document.addEventListener) {
                    document.addEventListener("WeixinJSBridgeReady", function () {
                        voicePromise =voice.play();
                    }, false);
                } else if ((document as any).attachEvent) {
                    (document as any).attachEvent("WeixinJSBridgeReady", function () {
                       voicePromise = voice.play();
                    });
                    (document as any).attachEvent("onWeixinJSBridgeReady", function () {
                       voicePromise= voice.play();
                    });
                }
            }

            //voiceStatu用來記録狀態,使 touchstart 事件只能觸發一次有效,避免與 click 事件衝突
            //监听 touchstart 事件进而调用 <audio> 元素提供的 play() 方法播放音频
            document.addEventListener("touchstart", function (e) {
               voicePromise = voice.play();
            }, false);
            if (voicePromise !== undefined) {
                voicePromise.then(_ => {
                    // 这个时候可以安全的暂停
                    voice.pause();
                }).catch(error => {

                });
            }
        }
export {
    autoPlayAudio
}