import React, { useLayoutEffect, useRef } from 'react'
import './index.css'
import starSky from '../../statics/voice/starsky.mp3'
import { useHistory } from 'react-router'
import { autoPlayAudio } from '../../utils/autoPlay'
const SubPage = () => {
  const audioRef = useRef<HTMLAudioElement>(null)
  const history = useHistory()

    useLayoutEffect(() => {
      document.title = '只属于你的礼物'
        autoPlayAudio(audioRef.current!)
        return () => {
            audioRef.current!.pause()
        }
  })
  const clickHandle = () => {
    history.push('/photo')

  }
  return (
    <div className={'subpage__wrap'}>
      <audio autoPlay={true} ref={audioRef} loop={true} preload="auto" src={starSky}></audio>
      <div className={'subpage__wrap-content'}>
        <h1 className={'content-title1'}> 突如其来的相遇，始料未及的欢喜</h1>
        <h2 className={'content-title2'}>世间的相遇,都是久别的重逢</h2>
              
        <span className={'content-title3'}>永远的18岁，祝你生日快乐😊</span>
      </div>
      <div onClick={clickHandle} className={'subpage__wrap-btn'}>go forward φ(≧ω≦*)♪</div>

      <h2 className={'fixedtitle'}>——最美的遇见</h2>
    </div>
  )
}
export default React.memo(SubPage)
