const words = [
    "❤清清🧚‍♀️，生日快乐～",
    "❉这是陪你度过的第一个生日,",
    "❉希望以后的每个生日都有我。(假想😊)",
    "❉我很感激缘分让我们相识,",
    "❉相谈之后发现我们有好多共同点,",
    "❉遇见你不容易,不要想着推开我,",
    "❉虽然给不了你多少感动，但我想以后可以都有我",
    "❉我也不知道为什么会喜欢你，喜欢你什么",
    "❉可是我就是喜欢你，很喜欢，非常喜欢你，越来越喜欢你",
    "❉我们就以最放松的方式去相处,不要去在意太多，想太多，",
    "❉想你每天都开森～",
    "❉You can trust me with your Ridge,",
    "❉And as long as you want to turn around, i^m standing behind you",
  ];
  let i = 0;
  let timer:any;
  // speed in ms
  let deleteDelay = 3000;
  let typeSpeed = 100;
  let delSpeed = 50;
  function typingEffect(el:any) {
    let word = words[i].split("");
    var loopTyping = function () {
      if (word.length > 0) {
        el.innerHTML += word.shift();
      } else {
        delay(function () {
          deletingEffect(el); // do stuff
        }, deleteDelay); // end delay
        // deletingEffect();
        return false;
      }
      timer = setTimeout(loopTyping, typeSpeed);
    };
    loopTyping();
  }

  function deletingEffect(el:any) {
    let word = words[i].split("");
    var loopDeleting = function () {
      if (word.length > 0) {
        word.pop();
        el.innerHTML = word.join("");
      } else {
        if (words.length > i + 1) {
          i++;
        }
        else {
          i = 0;
        }
        typingEffect(el);
        return false;
      }
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      timer = setTimeout(loopDeleting, delSpeed);
    };

    loopDeleting();
  }

  var delay = (function () {
    let timer:any = 0;
    return function (callback:any, ms:number) {
      clearTimeout(timer);
      timer = setTimeout(callback, ms);
    };
  })();
  export default typingEffect