import React, { useLayoutEffect, useRef } from 'react'
import {smallImages,bigImages} from  '../../utils/images'
import typingEffect from '../../utils/typing';
import nightyou from '../../statics/voice/nightyou.mp3'
import { useMounted } from '../../hooks'
import './index.css'
import { autoPlayAudio } from '../../utils/autoPlay';
const Photo = () => {
    const titleRef = useRef<any>('')
    const audioRef = useRef<HTMLAudioElement>(null)
    useMounted(() => {
        document.title = '记录最美的你😊'
        autoPlayAudio(audioRef.current!)
    })
    useLayoutEffect(()=>{
        typingEffect(titleRef.current)
        return () => {
            audioRef.current!.pause()
        }
    })
    const clickHandle = () => {
        window.location.href = 'https://absencesliet.github.io/lqbirthday/star/index.html'
    }
    return <>
        <audio autoPlay={true} ref={audioRef} loop={true} preload="auto" src={nightyou}></audio>
        <div className="typing">
            <div className="header-sub-title" ref={titleRef}>
            </div>
            <div className="header-sub-title blink">|</div>
        </div>
        <div className="finnalbtn" onClick={clickHandle}>
            Final surprise ໒(⊙ᴗ⊙)७✎▤
        </div>
        <div className="landscape"></div>
        <div className="filter"></div>
  <div className='wrap'>
    <div className="cube">
      <>
      {
        bigImages.map((img,index)=>
        <div className="out"  key={`b${index}`} >
        <img src={img} alt="" className='pic'/>
        </div>
        )
      }
      </>
      <>
      {
        smallImages.map((img,num)=>
        <span className="inner" key={`a${num}`}>
          <img src={img} alt=""  className='inner_pic'/>
        </span>
        )
      }
      </>
    </div>
  </div>
</>
  
    
  
}
export default React.memo(Photo)
